import { getSongListDetail, getSongListSongs, getSongListComments, getSongListSubscribers, getAlbumDetail, getAlbumDetailDynamic, getAlbumSongs, getAlbumComments } from '@/request/api/songListDetail';
import { postComment } from '@/request/api/songInfo';
import { mapMutations } from 'vuex';
export default {
  data() {
    return {
      // 是否显示加载中
      isLoading: 1,
      // 歌单id
      id: 0,
      // 页面类型
      type: 0,
      // 歌单简介是否已展开
      isDevelop: 0,
      // 歌单信息
      playlist: {},
      // 作者名称
      nickname: '',
      // 作者头像
      avatarUrl: '',
      // 作者 id
      userId: 0,
      // 搜索框默认文字
      defaultSearch: '搜索歌单音乐',
      // 在该歌单想查找的音乐
      searchKey: '',
      // 是否有标签
      isTags: 1,
      // 是否有简介
      isBrief: true,
      // 歌单简介是否有第二行
      isOther: 0,
      // 简介高度
      briefHeight: 20,
      // 简介第一行
      firstLine: '',
      // 歌单的所有歌曲
      songs: [],
      // 当前 tab，默认是歌单列表(1)
      songListDetailActive: 1,
      // 评论总数
      totalNum: 0,
      // 精彩评论
      hotComments: [],
      // 显示的精彩评论
      tenHotComments: [],
      // 最新评论
      latestComments: [],
      // 是否显示“更多精彩评论”
      isTab: 0,
      // 最大评论文字数
      maxlength: 140,
      // 评论剩余可输入长度
      leftover: 140,
      // 评论内容
      commentContent: '',
      // 收藏者
      subscribers: [],
      // 专辑信息
      album: {},
      // 专辑动态数据(专辑是否收藏,收藏数,评论数,分享数)
      albumDynamic: {}
    };
  },

  async mounted() {
    // console.log('接收到传过来的歌单id：' + this.$route.query.id);
    // 页面刷新而路由也不会丢失 id(在路由地址中拼接在末尾)
    this.id = this.$route.query.id; // 接收类型 1歌单 2专辑

    this.type = this.$route.query.type; // 歌单

    if (this.type == 1) this.getSongList(); // 专辑

    if (this.type == 2) this.getAlbum();
  },

  methods: {
    // 申请歌单数据
    async getSongList() {
      // 获取歌单详情
      const res = await getSongListDetail(this.id, sessionStorage.getItem("cookie")); // console.log('歌单详情', res);
      // 真正的歌单详情保存到组件

      this.playlist = res.data.playlist; // console.log('真正的歌单详情', this.playlist);

      this.userId = this.playlist.creator.userId || 0;
      this.nickname = this.playlist.creator.nickname || '云音乐私人雷达';
      this.avatarUrl = this.playlist.creator.avatarUrl || 'http://p1.music.126.net/4ZjO1oj0WTeN5U19FpnQFw==/109951165005238078.jpg?param=180y180'; // 判断是否有标签

      if (this.playlist.tags.length == 0) this.isTags = 0; // 判断是否有歌单简介

      if (!this.playlist.description) this.isBrief = 0; // 判断简介是否有第二行

      if (this.brief && this.brief.length > 1) this.isOther = 1; // 获取歌单里所有歌曲

      const res1 = await getSongListSongs(this.id, sessionStorage.getItem("cookie")); // console.log('歌单歌曲', res1);

      this.songs = res1.data.songs || []; // console.log('this.songs', this.songs);
    },

    // 申请专辑数据
    async getAlbum() {
      // 获取专辑内容
      const res = await getAlbumDetail(this.id, sessionStorage.getItem("cookie")); // console.log('获取专辑内容', res);
      // 真正的歌单详情保存到组件

      this.album = res.data.album; // console.log('专辑内容 album', this.album);

      this.userId = this.album.artist.id; // 获取专辑动态信息

      const res1 = await getAlbumDetailDynamic(this.id); // console.log('获取专辑动态信息', res1);

      this.albumDynamic = res1.data; // console.log('专辑动态信息', this.albumDynamic);
      // 获取专辑歌曲

      const res2 = await getAlbumSongs(this.id, sessionStorage.getItem("cookie")); // console.log('获取专辑歌曲', res2);

      this.songs = res2.data.songs || []; // console.log('this.songs', this.songs);
    },

    // 歌单简介缩放
    change(n) {
      if (n == 1) {
        // 展开
        this.isDevelop = 1;
        this.$refs.brief.classList = 'brief';
      } else {
        // 关闭
        this.isDevelop = 0;
        this.$refs.brief.classList = 'brief brief_height';
      } // 获取简介高度


      this.briefHeight = this.$refs.brief.offsetHeight;
    },

    // 评论发布时间的处理
    publishTime(time, timeStr) {
      // 检查是否昨天发布的
      // 定义正则查找规则，test() 如果字符串中包含规则则返回 true 否则 false
      const reg = /昨天/; // if (timeStr.indexOf('昨天') != -1)

      if (reg.test(timeStr)) {
        // console.log('昨天发布的：', timeStr);
        // '昨天'与时间中间加空格处理，字符串截取 slice() 包前不包后，左闭右开
        let newStr = timeStr.slice(0, 2) + ' ' + timeStr.slice(2, timeStr.length); // console.log(newStr);

        return newStr;
      } else {
        // 获取当前年份
        let YNow = new Date().getFullYear(); // 把返回的时间戳转为时间格式

        let date = new Date(time);
        let Y = date.getFullYear();
        let M = date.getMonth() + 1;
        let D = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
        let H = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
        let Min = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes(); // 今年发布的评论不需显示年份

        if (Y === YNow) return M + '月' + D + '日 ' + H + Min;else return Y + '年' + M + '月' + D + '日 ' + H + Min;
      }
    },

    // 对播放量的处理
    formatCount(num) {
      // 调用定义在自定义插件里的公共方法
      return this.$formatCount(num);
    },

    // 格式化单曲序号
    formatIndex(index) {
      return this.$formatIndex(index);
    },

    // 格式化播放时长
    formatDuration(time) {
      return this.$formatDuration(time);
    },

    // 时间戳转日期
    formatDate(time) {
      return this.$formatDate(time);
    },

    // 选择当前 Tab
    tabExclusive(n) {
      this.songListDetailActive = n; // 获取评论(第一次查看)

      if (n == 2 && this.tenHotComments.length == 0 && this.latestComments.length == 0) {
        this.isLoading = 1; // 歌单评论

        if (this.type == 1) this.getComments(this.id);
        if (this.type == 2) this.getAlbumComments(this.id);
      } else if (n == 3 && this.subscribers.length == 0) {
        this.isLoading = 1; // 获取歌单收藏者(第一次查看歌单收藏者)

        this.getSubscribers(this.id);
      } else return;
    },

    // 获取歌单评论
    async getComments(id) {
      const res = await getSongListComments(id, sessionStorage.getItem("cookie")); // console.log('获取歌单评论', res);

      this.totalNum = res.data.total;
      this.hotComments = res.data.hotComments; // 精彩评论如果超过十条则取前十显示，否则全取，isTab 需在 tenHotComments 之前赋值

      this.isTab = this.hotComments.length > 10 ? 1 : 0;
      this.tenHotComments = this.hotComments.length > 10 ? this.hotComments.splice(0, 10) : this.hotComments;
      this.latestComments = res.data.comments; // console.log('精彩评论', this.tenHotComments);
      // console.log('最新评论', this.latestComments);

      this.isLoading = 0;
    },

    // 获取专辑评论
    async getAlbumComments(id) {
      const res = await getAlbumComments(id, sessionStorage.getItem("cookie")); // console.log('获取专辑评论', res);

      this.totalNum = res.data.total;
      this.hotComments = res.data.hotComments; // 精彩评论如果超过十条则取前十显示，否则全取，isTab 需在 tenHotComments 之前赋值

      this.isTab = this.hotComments.length > 10 ? 1 : 0;
      this.tenHotComments = this.hotComments.length > 10 ? this.hotComments.splice(0, 10) : this.hotComments;
      this.latestComments = res.data.comments; // console.log('精彩评论', this.tenHotComments);
      // console.log('最新评论', this.latestComments);

      this.isLoading = 0;
    },

    // 获取歌单收藏者(默认60位)
    async getSubscribers(id) {
      const res = await getSongListSubscribers(id, sessionStorage.getItem("cookie"), 60); // console.log('歌单收藏者', res);
      // 歌单没有收藏者

      if (res.data.total == 0 && res.data.code == 200) {
        this.isLoading = 0;
      } else {
        this.subscribers = res.data.subscribers;
        if (this.subscribers.length !== 0) this.isLoading = 0; // console.log('收藏者们', this.subscribers);
      }
    },

    // 鼠标位于某个评论处
    mouseenter(index, type) {
      if (type === 1) this.$refs.report[index].style.display = 'block';
      if (type === 2) this.$refs.latestReport[index].style.display = 'block';
    },

    // 鼠标移出所有评论
    mouseleave(type) {
      if (type === 1) {
        for (let i = 0; i < this.$refs.report.length; i++) {
          this.$refs.report[i].style.display = 'none';
        }
      } else {
        for (let i = 0; i < this.$refs.latestReport.length; i++) {
          this.$refs.latestReport[i].style.display = 'none';
        }
      }
    },

    // 鼠标移入某个点赞
    zanMouseenter(index, type) {
      if (type === 1) {
        // this.$refs.zanIcon 与 this.$refs.zanNum 数组长度一致
        for (let i = 0; i < this.$refs.zanIcon.length; i++) {
          this.$refs.zanIcon[i].style.fill = '#545454';
          this.$refs.zanNum[i].style.color = '#a7a5a5';
        }

        this.$refs.zanIcon[index].style.fill = '#000';
        this.$refs.zanNum[index].style.color = '#000';
      } else {
        for (let i = 0; i < this.$refs.latestZanIcon.length; i++) {
          this.$refs.latestZanIcon[i].style.fill = '#545454';
          this.$refs.latestZanNum[i].style.color = '#a7a5a5';
        }

        this.$refs.latestZanIcon[index].style.fill = '#000';
        this.$refs.latestZanNum[index].style.color = '#000';
      }
    },

    // 鼠标移出所有点赞
    zanMouseleave(type) {
      if (type == 1) {
        for (let i = 0; i < this.$refs.zanIcon.length; i++) {
          this.$refs.zanIcon[i].style.fill = '#545454';
          this.$refs.zanNum[i].style.color = '#a7a5a5';
        }
      } else {
        for (let i = 0; i < this.$refs.latestZanIcon.length; i++) {
          this.$refs.latestZanIcon[i].style.fill = '#545454';
          this.$refs.latestZanNum[i].style.color = '#a7a5a5';
        }
      }
    },

    // 去往更多精彩评论页(携带歌单 id)
    goMoreHotComments() {
      this.$router.push({
        path: '/HotComments',
        query: {
          id: this.id,
          // 类型 1 为歌单的更多精彩评论，2 为专辑，3 为歌曲
          type: this.type
        }
      });
    },

    // 双击播放歌单列表
    playSongList(index) {
      // 显示底部音乐播放组件
      this.updateIsPlayBox(1); // 更新播放列表

      this.updatePlayList(this.songs); // 更新歌单名(歌曲来源)

      this.updateSongListName(this.playlist.name); // 更新歌单 id(歌曲来源)

      this.updateSongListId(this.playlist.id); // 更新播放列表下标

      this.updatePlayListIndex(index); // 更新歌曲总时长(歌单里对应歌曲的时长)
      // this.updateDuration(this.songs[index].dt)
    },

    // 去往个人主页
    gotoPersonalHome(userId) {
      this.$router.push({
        path: '/PersonalHome',
        query: {
          userId: userId
        }
      });
    },

    // 去往歌手页
    goSingerPage(id) {
      this.$router.push({
        path: '/SingerPage',
        query: {
          id: id
        }
      });
    },

    // 去往专辑详情页
    goAlbumDetail(id) {
      if (this.type == 1) {
        this.$router.push({
          path: '/SongListDetail',
          query: {
            id: id,
            // 1歌单 2专辑
            type: 2
          }
        });
      }
    },

    // 去往 mv播放页
    goVideoPlayer(mvid) {
      this.$router.push({
        path: '/VideoPlayer',
        query: {
          mvid: mvid,
          type: 1
        }
      });
    },

    // 发表评论
    async postComment() {
      // 生成当前时间的时间戳
      var timestamp = new Date().getTime();
      console.log(this.commentContent, this.commentContent.length, timestamp); // query 1是歌单，2是专辑，commentType 2是歌单，3是专辑

      var commentType = this.$route.query.type == 1 ? 2 : 3;
      const res = await postComment(1, commentType, this.id, this.commentContent, timestamp, sessionStorage.getItem("cookie")); // console.log('发送歌单/专辑评论', res);
      // console.log('发送歌单/专辑评论', res.data.comment);
      // 重新获取歌单评论，但不可马上获取到刚发布的评论
      // setTimeout(() => this.getComments(this.id), 1000)

      this.commentContent = '';
      alert('评论成功！');
    },

    ...mapMutations(['updateIsPlayBox', 'updatePlayList', 'updateSongListName', 'updateSongListId', 'updatePlayListIndex', 'updateHotCommentsType', 'updateDuration'])
  },
  computed: {
    // ！缺陷：连续换行两行的没处理，只能换单行
    // 对简介的处理
    brief() {
      let arr; // 以换行符分割每一行，每个 item 就是一行

      if (this.playlist.description) arr = this.playlist.description.split(/[(\r\n)\r\n]+/).map(item => item); // console.log(typeof(arr)); // object 数组也是对象

      this.firstLine = arr[0];
      return arr;
    },

    // 对专辑详情的分段处理
    albumDescription() {
      let arr;

      if (this.album.description) {
        // console.log('description', this.album.description);
        // 以换行符分割每一段，每个 item 就是一段
        arr = this.album.description.split(/[(\r\n)\r\n]+/).map(item => item);
      } // console.log('arr', arr);


      return arr;
    },

    // 顶部区域高度随简介高度变化(撑开下面的内容以显示简介部分)、利用计算属性动态设置样式
    topHeight() {
      return this.briefHeight + 164 + 'px';
    }

  },
  watch: {
    songs() {
      if (this.songs.length !== 0) this.isLoading = 0;
    },

    // 评论剩余可输入长度随着输入内容增多而减短
    commentContent() {
      this.leftover = 140 - this.commentContent.length;
    },

    // 监听路由参数 $route，路由参数改变则重新请求数据 和页面重新渲染
    '$route': function (newRoute, oldRoute) {
      // console.log('newRoute:', newRoute, 'oldRoute:', oldRoute);
      // console.log('newRoute:', newRoute.query.type, 'oldRoute:', oldRoute.query.type);
      this.id = newRoute.query.id; // 接收类型 1歌单 2专辑

      this.type = newRoute.query.type; // 歌单

      if (this.type == 1) this.getSongList(); // 专辑

      if (this.type == 2) this.getAlbum();
    }
  }
};